import React ,{Component} from 'react';
import {Card} from 'react-bootstrap';
import Statedata from './Statedata';

class India extends Component{


    constructor(){
        super();
    }


    render(){
        return(
            <div className="row">
             <div className="col-md-12">
               <h3>INDIA</h3>

             </div>
             <div className="col-md-12">
                <div className="row">
                    <div className="col-md-3">

                    <Card className="badge  "  bg="danger"  style={{ width: '18rem' }}>
  
                    <Card.Body className="text-center">
                    <Card.Title>TOTAL CASES</Card.Title>
                    <h3>128499</h3>

                    
                    <Card.Text>
                        [TODAY:25]
     
                    </Card.Text>
    
                    </Card.Body>
                    </Card>


                    </div>

                    <div className="col-md-3">

                    <Card className="badge  "  bg="warning"  style={{ width: '18rem' }}>
  
                    <Card.Body className="text-center">
                    <Card.Title>ACTIVE CASES</Card.Title>
                    <h3>128499</h3>

                    
                    <Card.Text>
                        [TODAY:25]
     
                    </Card.Text>
    
                    </Card.Body>
                    </Card>


                    </div>

                    <div className="col-md-3">

                    <Card className="badge  "  bg="success"  style={{ width: '18rem' }}>
  
                    <Card.Body className="text-center">
                    <Card.Title>RECOVERD</Card.Title>
                    <h3>128499</h3>

                    
                    <Card.Text>
                        [TODAY:25]
     
                    </Card.Text>
    
                    </Card.Body>
                    </Card>


                    </div>

                    <div className="col-md-3">

                    <Card className="badge  "  bg="info"  style={{ width: '18rem' }}>
  
                    <Card.Body className="text-center">
                    <Card.Title>DEATHS</Card.Title>
                    <h3>128499</h3>

                    
                    <Card.Text>
                        [TODAY:25]
     
                    </Card.Text>
    
                    </Card.Body>
                    </Card>


                    </div>
                    
                </div>

             </div>
               
            <div className="col-md-12">
                <Statedata/>
            </div>

           </div>
        )
    }
}

export default India;